* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

img {
  width: 100%;
  height: 100%;
  max-width: unset;
  object-fit: cover;
  display: block;
}

#search-form {
  font: inherit;
  max-width: 300px;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px;
}

input {
  height: 30px;
}

button[type="submit"] {
  min-width: 80px;
  height: 30px;
  cursor: pointer;
  color: #fafafa;
  background-color: #0095ff;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  max-width: calc(25% - 15px);
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.photo {
  max-width: 100%;
  height: auto;
  background-size: cover;
}

.info {
  justify-content: space-around;
  gap: 15px;
  padding: 10px 10px 10px 5px;
  display: flex;
}

.info-item {
  max-width: calc(25% - 11.25px);
  flex-direction: column;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
}

.info-text {
  margin-bottom: 5px;
  display: inline-block;
}

.load-more {
  min-width: 100px;
  height: 30px;
  color: #fafafa;
  cursor: pointer;
  background-color: #0095ff;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

